@import url('https://fonts.googleapis.com/css?family=Ubuntu:400,500');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html {
    font-size: 16px;
}
body {
    font-size: 1rem;
    font-family: 'Ubuntu', sans-serif;
}
h1, h2, h3 {
    font-weight: 500;
}
.img-fluid {
    max-width: 100%;
    width: 100%;
    height: auto;
}
.red-info {
    color: darkred;
}
.py-1 {
    padding: 1rem 0;
}
.py-3 {
    padding: 3rem 0;
}
.row-flex {
    display: flex;
}


.app-container {
    min-height: 100vh;
    grid-template-rows: auto 1fr auto;
}
.container {
    max-width: 1200px;
    margin: auto;

}
.top-header {
    margin-bottom: 2rem;
}

.main-box {
    display: grid;
    grid-template-columns:  3fr 1fr;
}

