/* ================== filter-box */
.filter-box {
    padding: 1rem;
    background: lightgray;
    margin-bottom: 1rem;
    display: flex;
}
.filter-label {
    margin-right: 1rem;
}
.select-control {
    background: none;
    border: 1px solid navy;
    margin-right: 2rem;
}


